@import url("https://fonts.googleapis.com/css2?family=Commissioner:wght@400;700&display=swap");

.main-view {
  font-family: "Commissioner", sans-serif;
  font-weight: 700;
  margin-top: 18px;
  margin-bottom: 10px;
}

.navbar {
  font-family: "Commissioner", sans-serif;
  font-weight: 700;
}

#button {
  background-color: #ffbebe;
  color: #000;
  border: none;
  border-radius: 12px;
}

#button:hover,
#button:focus,
#button:active {
  background-color: #ffcece;
  color: #222;
}
